
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { getBars, deleteBar, isSubscribe } from '@/api/'
import BpDialog from '../components/bpDialog'
import InlineLoading from '../components/InlineLoading'
export default {
data () {
return {
confirmVisible: false,
moneyInfo: {},
barList: [],
managerList: [],
deleteInfo: null,
loading: true,
myScroll: null,
concernVisible: false,
ticket: ''
}
},
beforeRouteEnter (to, from, next) {
document.title = '商户管理'
next()
},
created () {
isSubscribe({ht_id: 0, type: 2, url: window.location.hash.substring(1)}).then((res) => {
if (res.result === '已关注') {
this.concernVisible = false
} else {
this.ticket = res.result
this.concernVisible = true
}
})
getBars().then((res) => {
this.moneyInfo = res.result.money
this.barList = res.result.hotelList.merchant_hotel_list
this.managerList = res.result.hotelList.manage_hotel_list
this.loading = false
})
},
mounted () {
},
methods: {
confirmDelete () {
deleteBar({ht_id: this.deleteInfo.id}).then((res) => {
let origin = Object.assign([], this.barList)
origin.splice(this.deleteInfo.index, 1)
this.barList = []
this.$nextTick(() => {
this.barList = origin
})
this.confirmVisible = false
})
},
onDeleteBar (index, id) {
this.confirmVisible = true
this.deleteInfo = {
index: index,
id: id
}
},
goToBar (barId) {
this.$router.push({
path: `/BusinessCenter`,
query: {
id: barId,
type: 2
}
})
}
},
filters: {
fixedPrice (v) {
if (!Number.isNaN(Number(v))) {
return v.toFixed(2)
}
}
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
BpDialog,
InlineLoading,
XDialog
}
}



